import React from 'react'

class AyxFooter extends React.Component {
	render(){
		const date = new Date();
		const year = date.getFullYear();
			return(

				<footer className="bggreen mt-3">
				<div className="col-md-12 text-center pt-3 pb-3">
				&copy; {year} </div>
				</footer>
			

				)


	}
}

export default AyxFooter