import React from 'react'

function Signup() {

	return(

			<div className="container">
			<div className="row">

			<div className="col-md-12 mt-5">
			<center>
			<div className="col-md-6 mt-5 mb-5">
			<div className="bg-info"><h2 className="pt-1 pb-1 text-light">Sign up</h2></div>	
			<form>
			<div className="input-group">
			<input type="text" className="form-control mt-2 mb-2" placeholder="Username" /> 
			</div>
			<div className="input-group">
			<input type="password" className="form-control mb-2 mt-2" placeholder="Password" /> 
			</div>

			<div className="input-group">
			<input type="Password" className="form-control mb-2 mt-2" placeholder="Confirm Password" /> 
			</div>

			<input type="submit" className="btn btn-outline-info" />
			</form>
			</div>
			</center>
			</div>
			</div>
			</div>


		)
}

export default Signup 