import React from 'react'

class Calc extends React.Component {
	constructor() {
		super()
		this.state = {
value: '',
sign: "",
val: "",
ans: "",
		
		}
}
Change = (event) => {
this.state.sign ? this.setState({val: this.state.val + event.target.id}) : this.setState({value: this.state.value + event.target.id})
	
}

Change2 = (event) => {

	this.setState({sign: event.target.id})
}
getAns = () => {

	var x = parseInt(this.state.value)
	var y = parseInt(this.state.val)

	if(this.state.sign === "+" && this.state.value && this.state.val) {

	this.setState({ans: String(x + y)})

} else if(this.state.sign === "-" && this.state.value && this.state.val) {

	this.setState({ans: String(x - y)})

}
else if(this.state.sign === "/" && this.state.value && this.state.val) {

	this.setState({ans: String(x / y)}) 

}

if(this.state.sign === "*" && this.state.value && this.state.val) {

	this.setState({ans: String(x * y)})

}
}

Clear = () => {
	this.setState({val: "",
	value: "",
	sign: "",
	ans: "",
})
}
		render() {
			return(
				<div className='col-md-12'>
				<center>
				<div className='col-md-6 mt-5 pt-5'>
				<input placeholder="Answer" className="form-control text-center" type='text' value={this.state.ans} readOnly/>
				<input className="form-control mb-5" style={{padding: "30px"}} type='text' value={this.state.value + " " + this.state.sign + " " + this.state.val} readOnly />
				<div className='btn btn-outline-primary btn-sm' onClick={this.Change} id="1">1</div>
				<div className='btn btn-outline-primary btn-sm' onClick={this.Change} id="2">2</div>
				<div className='btn btn-outline-primary btn-sm' onClick={this.Change} id="3">3</div>
				<div className='btn btn-outline-primary btn-sm' onClick={this.Change} id="4">4</div>
				<div className='btn btn-outline-primary btn-sm' onClick={this.Change} id="5">5</div>
				<div className='btn btn-outline-primary btn-sm' onClick={this.Change} id="6">6</div>
				<div className='btn btn-outline-primary btn-sm' onClick={this.Change} id="7">7</div>
				<div className='btn btn-outline-primary btn-sm' onClick={this.Change} id="8">8</div>
				<div className='btn btn-outline-primary btn-sm' onClick={this.Change} id="9">9</div>
				<div className='btn btn-outline-primary btn-sm' onClick={this.Change} id="0">0</div>
				<span className="btn btn-sm btn-outline-primary" onClick ={this.Clear}>Clear</span>
				<br />
				<div className='btn btn-outline-primary btn-sm mt-1 mr-1' onClick={this.Change2} id="+">+</div>
				<div className='btn btn-outline-primary btn-sm mt-1 mr-1' onClick={this.Change2} id="-">-</div>
				<div className='btn btn-outline-primary btn-sm mt-1 mr-1' onClick={this.Change2} id="/">/</div>
				<div className='btn btn-outline-primary btn-sm mt-1' onClick={this.Change2} id="*">*</div>
				<br />
					<div className='btn btn-outline-primary btn-sm mt-1' onClick={this.getAns} id="="> = </div>
				</div>
				</center>
				</div>
				)


		}
	}


export default Calc