import React from 'react'

class AyxHeader extends React.Component {
	constructor() {
		super()
			
	}

	render() {
		function show() {
	const bn = document.getElementById('nav');
	bn.classList.toggle("seen");
}
				
		return(
				<header className="container-fluid" id="nv">
				<nav className="row">
				<div className="col-sm-12 navbar">
				<span id="lgt"><h4 style={{color: "#17a2b8"}}>
				Ayx Graphics <button onClick={show} className="text-light" 
				style={{border:"3px", borderStyle: "solid", borderColor: "#17a2b8", background: "none"}} 
				id="bn">
				<i className="fa fa-bars"></i></button> </h4> 	
			    </span>
				<ul className="mt-3" id="nav">
				<li onClick={this.props.getHome}>Home<i className="fa fa-home"></i></li>
				<li onClick={this.props.getContact}>Contact Us<i className="fa fa-address-book"></i></li>
				<li onClick={this.props.getLogin}>Login<i className="fa fa-user"></i></li>
				<li onClick={this.props.getSignup}>Sign up<i className="fa fa-user-plus"></i></li>
				<li onClick={this.props.getCalc}>Calculator<i className="fa fa-user-plus"></i></li>
				</ul>

				</div>
				</nav>
					

				</header>



			)
	}
}

export default AyxHeader
