import React from 'react'
import new1 from './new1.jpg'
import minionss from './minionss.png'
import Ae from './Ae.jpg'


function Home(props) {
	return(
	<section>
					<div className="hero text-light" id="imgBg">
<span className="col-sm-12"><center>
<h1 className="font-weight-bold text-info display-4">About Ayx Graphics</h1>
<h5 className="font-weight-bold">We specialize in all types of graphics, logos, banners, flyers etc.</h5>
<button className="btn btn-outline-info mt-3" onClick={props.getContact}>Contact Us</button></center>
</span>
</div>

<div className="container-fluid">
<div className="row">

<div className="col-sm-12">
	
<center><h1 className="mt-3 font-weight-bold text-capitalize">About the Founder</h1></center>
<hr style={{width: "80%"}} />
</div>
	<div className="col-sm-12">
		<center>
		<img src={new1} className="img-thumbnail" width="220px" height="190px" alt="founder" />
		<div className="col-md-6 text-justify-center mt-3">
			<h4 className="font-weight-bold">Ayomide Oloyede</h4>
			<p>He is a graphic designer and web developer with skills in coreldraw and photoshop. This site is coded with React Js.</p>
			<p className="mt-3"><button className="btn btn-outline-info btn-sm" onClick={props.getContact}>Contact us</button></p></div></center>
		</div></div>
</div>

<div className="row mr-0 bggreen ">
		<div className="col-md-12 mt-2"><h2 className="text-center">Some Graphic designs</h2></div>
<div className="col-md-4 media pr-0">
<center className="container-fluid">
<div className="col-md-6 mb-3 mt-3 pt-2 pb-2">
<img src={minionss} className="img-thumbnail mr-3" width="220px" height="190px" alt="flyer design" />
<div className="media-body">
	<h5 className="mt-1 font-weight-bold">Flyer design</h5>
<p className=" text-secondary">We specialize in designing flyers for adverts and invitations. With professional looks and easy to read.</p></div></div></center>
</div>	

<div className="col-md-4 media pr-0">
<center className="container-fluid">
<div className="col-md-6 mb-3 mt-3 pt-2 pb-2">
<img src={new1} className="img-thumbnail" width="220px" height="190px" alt="vector art" />
<div className="media-body">
	<h5 className="mt-1 font-weight-bold">Vector Art</h5>
<p className=" text-secondary">We can turn your images (raster) into vector or cartoon-like images.</p></div></div></center>
</div>	


<div className="col-md-4 media pr-0">
<center className="container-fluid">
<div className="col-md-6 mb-3 mt-3 pt-2 pb-2">
<img src={Ae} className="img-thumbnail" width="220px" height="190px" alt="logo design" />
<div className="media-body">
	<h5 className="mt-1 font-weight-bold">Logo design</h5>
<p className=" text-secondary">We can also create your company brand logo, to look professional and standard</p></div></div></center>
</div>	
</div>

<div className="col-sm-12 text-justify-right text-center mt-3 pt-2">
	<p>Contact us to get to know more about our services..</p><button onClick={props.getContact} className=" btn btn-outline-info">Write Us</button>
	</div>


				</section>



		)
}

export default Home