import React from 'react'

function Login() {
	return(
		<div className="container">
		<div className="row">

		<div className="col-md-12 mt-5">
		<center>
		<div className="col-md-6 mt-5 mb-5">
		<div className="bg-info"><h2 className="pt-1 pb-1 text-light">Login</h2></div>	
		<form>
		<div className="input-group">
		<input type="text" name="" className="form-control mt-2 mb-2" placeholder="Username" /> 
		</div>
		<div className="input-group">
		<input type="password" name=""  className="form-control mb-2 mt-2" placeholder="Password" /> 
		</div>
		<input type="submit" className="btn btn-outline-info" />
		<a className="nav-link" href='#'>Click here to sign up</a>
		</form>
		</div>
		</center>
		</div>
		</div></div>

		)
}

export default Login