import React from 'react';
import './App.css';
import AyxHeader from './components/AyxHeader'
import AyxFooter from './components/AyxFooter'
import Main from './components/Main'
import Login from './components/Login'
import Home from './components/Home'

class App extends React.Component {
constructor() {
  super()
 this.state = {
      Home: true,
      Contact: false,
      Login: false,
      SignUp: false,
      Calc: false
    }
}

getHome = () => {

this.setState({Home: true})
}

getSignup = () => {
  this.setState({
 Home: false,
      Contact: false,
      Login: false,
      SignUp: true,
      Calc: false

  })
}

getLogin = () => {
  this.setState({
 Home: false,
      Contact: false,
      Login: true,
      SignUp: false,
      Calc: false

  })
}


getContact = () => {

  this.setState({
     Home: false,
      Contact: true,
      Login: false,
      SignUp: false,
      Calc: false
  })
}
getCalc = () => {
  this.setState({
     Home: false,
      Contact: false,
      Login: false,
      SignUp: false,
      Calc: true
  })
}

  render() {



return(
     <section style={{fontFamily: "Yeon Sung"}}>
      <AyxHeader getCalc={this.getCalc} getHome={this.getHome} getLogin={this.getLogin} getSignup={this.getSignup} getContact={this.getContact} />
      <Main Calc={this.state.Calc} Home={this.state.Home} Register={this.state.SignUp} Login={this.state.Login} Contact={this.state.Contact} getContact={this.getContact} />
      <AyxFooter />
     </section>
    )
  }
}

export default App;